<template><section><h1>FrustumGeometry、 FrustumOutlineGeometry</h1>
<ul>
<li><code v-pre>vc-geometry-frustum</code> 组件用于加载视锥体。</li>
<li><code v-pre>vc-geometry-outline-frustum</code> 组件用于加载视锥体轮廓。</li>
<li>需要作为 <code v-pre>vc-instance-geometry</code> 的子组件使用，可以挂载到 <code v-pre>vc-primitive</code>。</li>
</ul>
<h2>示例</h2>
<h3>加载视锥体</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready" @LEFT_CLICK="LEFT_CLICK">
        <vc-primitive :appearance="appearance">
          <vc-instance-geometry :geometry.sync="geometry" :attributes="attributes">
            <vc-geometry-frustum ref="frustum" :frustum="frustum" :origin="origin" :orientation="orientation" :vertexFormat="vertexFormat"></vc-geometry-frustum>
          </vc-instance-geometry>
        </vc-primitive>
        <vc-primitive :appearance="appearance">
          <vc-instance-geometry :attributes="attributes">
            <vc-geometry-outline-frustum ref="frustumOutline" :frustum="frustum" :origin="originOutline" :orientation="orientation"></vc-geometry-outline-frustum>
          </vc-instance-geometry>
        </vc-primitive>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
