            : geometry.boundingSphere
          <span class="hljs-keyword">return</span> prev === <span class="hljs-literal">null</span> ? boundingSphere : Cesium.BoundingSphere.union(prev, boundingSphere)
        }, <span class="hljs-literal">null</span>)
        instances[<span class="hljs-number">0</span>].viewer.scene.camera.flyToBoundingSphere(boundingSphereUnion)
      })
