        <span class="hljs-built_in">console</span>.log(feature)
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h2>属性</h2>
<h3><code v-pre>vc-geometry-frustum</code></h3>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>frustum</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定视锥体参数。</td>
</tr>
<tr>
<td>origin</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定视锥体原点。</td>
</tr>
<tr>
<td>orientation</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定视锥体旋转参数。</td>
</tr>
<tr>
<td>vertexFormat</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定视锥体顶点渲染方式。</td>
</tr>
</tbody>
</table>
<hr>
<h3><code v-pre>vc-geometry-outline-frustum</code></h3>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>frustum</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定视锥体参数。</td>
</tr>
<tr>
<td>origin</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定视锥体原点。</td>
</tr>
<tr>
<td>orientation</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定视锥体旋转参数。</td>
</tr>
</tbody>
</table>
<p>参考官方文档：<strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/FrustumGeometry.html">FrustumGeometry</a></strong>、 <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/FrustumOutlineGeometry.html">FrustumOutlineGeometry</a></strong></p>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data() {
        return {
          appearance: null,
          geometry: null,
          attributes: null,
          vertexFormat: null,
          orientation: { x: 0, y: 0, z: 0, w: 1},
          frustum: null,
          origin: { lng: 105, lat: 35 },
          originOutline: { lng: 110, lat: 35 }
        }
      },
      mounted () {
        Promise.all([
          this.$refs.frustum.createPromise,
          this.$refs.frustumOutline.createPromise,
        ]).then(instances => {
          console.log('All geometries are loaded.')
          const boundingSphereUnion = instances.reduce((prev, cur) => {
            const geometry = cur.cesiumObject.constructor.createGeometry(cur.cesiumObject)
            const boundingSphere = cur.vm.$parent.modelMatrix
              ? Cesium.BoundingSphere.transform(geometry.boundingSphere, cur.vm.$parent.modelMatrix)
              : geometry.boundingSphere
            return prev === null ? boundingSphere : Cesium.BoundingSphere.union(prev, boundingSphere)
          }, null)
          instances[0].viewer.scene.camera.flyToBoundingSphere(boundingSphereUnion)
        })
      },
      methods: {
        ready({ Cesium, viewer }) {
          this.viewer = viewer
          const { PerInstanceColorAppearance, Cartesian3, ColorGeometryInstanceAttribute, PerspectiveFrustum, Quaternion } = Cesium
          this.appearance = new PerInstanceColorAppearance({
            flat : true
          })
          this.vertexFormat = PerInstanceColorAppearance.VERTEX_FORMAT
          this.attributes = {
            color: new ColorGeometryInstanceAttribute(Math.random(), Math.random(), Math.random(), 0.5)
          }
          this.frustum = new PerspectiveFrustum({
            fov: Cesium.Math.toRadians(30.0),
            aspectRatio: 1920.0 / 1080.0,
            near: 1.0,
            far: 500000
          })
        },
        LEFT_CLICK(movement) {
          const feature = this.viewer.scene.pick(movement.position)
          console.log(feature)
        }
      }
    }
  </script>